class Video{
    constructor(){

    }

    /**
     * [formatTime 格式化时间]
     * @param  {[String]} time [old time]
     * @return {[String]}      [new format time]
     */
    formatTime(time){
        //128s -> 00:02:08
        time*=1;
        var stime = '';
        var s = '';
        var m = '';
        var h = '';

        s = this.addZoom(Math.ceil(time % 60));  
        m = this.addZoom( ((time / 60) % 60) < 1 ? 0 : Math.ceil((time / 60) % 60)) ;
        h = this.addZoom((time / 60 / 60) < 1 ? 0 : Math.ceil((time / 60 / 60))  );
        return h+':'+m+':'+s;
    }

    /**
     * [addZoom 小于10补0成两位数]
     * @param {[String]} num [new two digits]
     */
    addZoom(num){
        if(num*1 > 9){
            return ''+num
        }else{
            return '0'+num;
        }
    }

}


export default (new Video()); 


