import oVideo,{ videoLists } from './video.js'
import qs from 'qs';

export default {
  data(){
    return{
      videoDatas:{
        title: '欢迎来到skylander影院',
        url:'',
        views: '',
        likes: '',
        comments: ''
      },
      isPlay: false,
      animationTimer: null,
      currentTime: '00:00:00',
      duration: '00:00:00',
      isEnded: false,
      thisVideo: null,
      isLoaded: false,
      videoList: [],
      currentVideoIndex: 0,
      searchUrl: 'http://localhost/learn/youtube/data/data.php'
    }
  },
  methods:{
    //初始化视频列表数据
    initVideoList(){
      this.videoList = videoLists;
      //this.videoDatas = JSON.parse(JSON.stringify(videoLists[0]));
      this.videoDatas = Object.assign({},videoLists[this.currentVideoIndex]);
    },
    initData(){
      
      this.$axios.get(this.searchUrl,{
        params: {
            part: 'snippet',
            q: 'newest movie',
            type: 'video',
            maxResults: '25',
            key: 'AIzaSyBw7mZHKiWd6Rx58dV7X2aa-aT_py8x0kM'
        }
      }).then((res)=>{
        console.log(res);
      })

      /*this.$axios.get('http://localhost/learn/youtube/sucai/youtube.api.php?url=https://www.youtube.com/watch?v=YGCLs9Bt_KY').then((res)=>{
        console.log(res);
      }).catch(err=>{
        console.log(err);
      });*/
    },
    initVideoPlay(){
      this.videoDatas = Object.assign({},videoLists[this.currentVideoIndex]);
      this.thisVideo.pause();
      this.isPlay = false;
      this.thisVideo.load();
      this.thisVideo.addEventListener('loadedmetadata',(e)=>{
        this.thisVideo.play();
        this.isPlay = true;
      });
      $('.vlist-item').removeClass('vlist-item-active')
      $('.vlist-item').eq(this.currentVideoIndex).addClass("vlist-item-active");
    },
    videoControl(){
      this.thisVideo = $('#play-video-source').get(0);
      let video = this.thisVideo;
      let playBth = $('.player-toggle-video');
      let lastVideoBtn = $('.player-last-video');
      let nextVideoBtn = $('.player-next-video');
      let lastVideoTime = $('.player-last-time');
      let nextVideoTime = $('.player-next-time');

      //播放暂停
      playBth.click((e)=>{

        if(!this.isPlay){
          video.play();
        }else{
          video.pause();
        }

        this.isPlay = !this.isPlay;
        e.stopPropagation();
        return false;
      });

      //去到上各时间段的时间点
      let timeDis = 10;
      lastVideoTime.click((e)=>{
        video.currentTime -= timeDis;
        if(video.currentTime < 0){
          video.currentTime = 0;
        }
      })
      nextVideoTime.click((e)=>{
        video.currentTime += timeDis;
        if(video.currentTime > video.duration){
          video.currentTime = video.duration;
        }
      })


      //上一个视频
      $('.player-last-video').click((e)=>{
        this.currentVideoIndex--;
        if(this.currentVideoIndex < 0){
          this.currentVideoIndex = 0;
        }
        this.initVideoPlay();
      })

      //下一个视频
      $('.player-next-video').click((e)=>{
        this.currentVideoIndex++;
        if(this.currentVideoIndex > this.videoList.length-1){
          this.currentVideoIndex = this.videoList.length-1;
        }
        this.initVideoPlay();
      })

    },
    videoEventInit(){
      let video = this.thisVideo;
      let This = this;

      //开始加载视频帧
      video.addEventListener('loadedmetadata',()=>{
        this.isLoaded = true;
        This.duration = oVideo.formatTime(video.duration);
        getCtime();
      });

      //视频播放
      video.addEventListener('play',()=>{
        //检测是否视频停止
        if(This.isEnded){
          getCtime();
          This.isEnded = false;
        }

        if(This.isLoaded){
          getCtime();
        }

      });

      //视频暂停
      video.addEventListener('pause',()=>{
        This.isPlay = false;
        cancelAnimationFrame(This.animationTimer);
      });

      //视频结束
      video.addEventListener('ended',()=>{
        cancelAnimationFrame(This.animationTimer);
        This.isEnded = true;
        This.isPlay = false;
      })

      //根据当前时间计算
      function getCtime(){
        This.animationTimer = requestAnimationFrame(getCtime);
        var ctime = oVideo.formatTime(video.currentTime);
        //console.log(ctime)
        This.currentTime = ctime;
        This.calcProgress();
      }
    },
    calcProgress(){
      let video = this.thisVideo;
      let videoProgressBox = $('.progress-box');
      let videoProgress = $('.progress-line');
      let vprogressBtn = $('.progress-line-btn');

      let w = Math.ceil((video.currentTime/video.duration)*videoProgressBox.width());

      videoProgress.width(w);
      vprogressBtn.css('left',w);
    },
    //进度条交互
    progressInteractive(){
      let video = this.thisVideo;
      var This = this;
      let videoProgressBox = $('.progress-box-wrap');
      let videoProgress = $('.progress-line');
      let vprogressBtn = $('.progress-line-btn');
      let videoWrap = $('.video-content-wrap');

      //点击滚动条交互
      videoProgressBox.on('mousedown',(e)=>{
        var oP = e.offsetX/videoProgressBox.width()*video.duration;
        if(this.isLoaded){
          video.currentTime = oP;
          videoProgress.width(e.offsetX);
          vprogressBtn.css('left',e.offsetX);
        }
      });

      dragMotion();
      //拖动交互
      function dragMotion(){
        
        var sX = 0,oX = 0,cX = 0,cw = 498;

        //拖动进度条bar
        vprogressBtn.on('mousedown',fn);

        //1.fn
        function fn(e){
          
          if(!This.isLoaded){
            return false;
          }

          sX = e.clientX;
          oX = $(this).offsetParent().offset().left;
          cX = sX - oX;

          if(This.isPlay){
            video.pause();
            cancelAnimationFrame(This.animationTimer);
          }

          $(document).on('mousemove',fn1);

          $(document).on('mouseup',fn2);

          e.stopPropagation();
        }

        //2. fn1
        function fn1(e){
          var disX = e.clientX - sX + cX;
          
          if(disX < 1){
            disX = 1;
          }
          if(disX > cw){
            disX = cw;
          }

          vprogressBtn.css('left',disX);
          videoWrap.css('filter','blur(3px)');

          //设置视频当前的时间
          video.currentTime = disX/cw*video.duration;
          videoProgress.width(disX);

          e.stopPropagation();
        }

        //3. fn2
        function fn2(e){
          //视频播放
          if(!This.isPlay){
            video.play();
            This.isPlay = true;
            videoWrap.css('filter','blur(0px)');
          }

          $(document).off('mousemove',fn1);
          $(document).off('mouseup',fn2);
          e.stopPropagation();
        }
      }
    },
    //声音进度条交互
    voiceInteractive(){
      let video = this.thisVideo;
      let This = this;
      let Wrap = $('.progress-voice-box');
      let line = $('.progress-voice-line');
      let circle = $('.progress-voice-line-btn');

      dragMotion();

      //拖动交互
      function dragMotion(){
        
        var sX = 0,oX = 0,cX = 0,cw = 44;

        //拖动进度条bar
        circle.on('mousedown',fn);

        //1.fn
        function fn(e){
          
          sX = e.clientX;
          oX = $(this).offsetParent().offset().left;
          cX = sX - oX;

          $(document).on('mousemove',fn1);
          $(document).on('mouseup',fn2);
          e.stopPropagation();
        }

        //2. fn1
        function fn1(e){
          var disX = e.clientX - sX + cX;
          
          if(disX < 0){
            disX = 0;
          }
          if(disX > cw){
            disX = cw;
          }

          circle.css('left',disX);
          video.volume = disX/cw;
          //设置视频当前的时间
          line.width(disX);
          e.stopPropagation();
        }

        //3. fn2
        function fn2(e){
          $(document).off('mousemove',fn1);
          $(document).off('mouseup',fn2);
          e.stopPropagation();
        }

      }

    },
    cutStr(str,len){
      return str.length < len ? str :  str.substr(0,len)+'...';
    },
    //通过视频ID播放视频
    playVideoById(index){
      let video = this.thisVideo;
      var data = this.videoList;
      


      this.currentVideoIndex = index;
      this.videoDatas.url = data[index].url;
      this.videoDatas.title = data[index].title;
      this.videoDatas.thumb = data[index].thumb;

      this.initVideoPlay();
      
    }
  },
  created(){
    this.initData();
    this.initVideoList();
  },
  mounted(){
    this.videoControl();
    this.videoEventInit();
    this.progressInteractive();
    this.voiceInteractive();
  }
};


