<template>
  <div class="video-container" ref="container">
      
      <div class="video-wrapper">

        <!-- video play Layer -->
        <div class="video-content-wrap">
            <img :src="videoDatas.thumb"  class="posterImage" />
            <video id="play-video-source" class="video-js vjs-default-skin" preload="none" width="640" height="264" 
                data-setup="{}" >
                <source :src="videoDatas.url" type='video/mp4' />
                <source :src="videoDatas.url" type='video/webm' />
               <!--<source :src="videoDatas.url" type='video/ogg' /> -->
            </video>
        </div><!-- // end video-content-wrap -->
        
        <!-- video blur Layer -->  
        <div class="video-blur-Layer"></div>

        <!-- UI controls Layer -->
        <div class="video-control-wrap">
          <section class="vc-header">
            <span class="vc-header-title">{{this.videoDatas.title}}</span>
            <span class="vc-header-listbtn">
              <i class="vh-listItem"></i>
              <i class="vh-listItem"></i>
              <i class="vh-listItem"></i>
            </span>
          </section><!--//end vc-header-->
          <section class="vc-footer">
              <div class="vf-controls-container">
                
                <!--// 进度条-->
                <div class="progress-box-wrap">
                  <section class="progress-box">
                    <div class="progress-line"></div>
                    <span class="progress-line-btn"></span>
                  </section>
                </div>
                

                <!--// time -->
                <section class="video-current-time">
                  <i class="current-t">{{currentTime}}</i> / <i class="current-e">{{duration}}</i>
                </section>
                
                <!--// 播放控制按钮 -->  
                <section class="video-control-btns">
                  <span class="player-last-video"></span>
                  <span class="player-last-time"></span>
                  <span class="player-toggle-video">
                    <i class="play-icon" v-show="!isPlay"></i>
                    <i class="pause-icon" v-show="isPlay"></i>
                  </span>
                  <span class="player-next-time"></span>
                  <span class="player-next-video"></span>
                </section>
                
                <!--// 声音控制按钮 -->
                <section class="voice-controls">
                  <i class="voice-icon"></i>
                  <div class="progress-voice-box">
                    <div class="progress-voice-line"></div>
                    <div class="progress-voice-line-btn"></div>
                  </div>
                  <span class="progress-line-btn voice-progress-line-btn"></span>
                </section>

              </div>
          </section><!--//end vc-footer-->
        </div><!-- // end video-control-wrap -->
        
        <!-- Video List Layer -->
        <div class="video-lists-wrap">
          <section class="vl-header">
            <h3>热门推荐</h3>
          </section>
          <section class="vl-body">
            <ul class="vl-body-lists">
              <li :class="(item.id==videoDatas.id) ? 'vlist-item vlist-item-active':'vlist-item'" v-for="item,index in videoList" :data-id="item.id" @click.stop="playVideoById(index)">
                <dl>
                  <dt class="video-image-wrap">
                    <img :src="item.thumb" />
                    <i class="video-icon"></i>
                  </dt>
                  <dd class="video-content-wrap">
                    <h3 class="list-video-title">{{cutStr(item.title,15)}}</h3>
                    <p class="list-video-content">{{cutStr(item.des,25)}}</p>
                    <div class="list-show-icons">
                      <span class="video-si-item video-views">{{item.views}}</span>
                      <span class="video-si-item video-likes">{{item.likes}}</span>
                      <span class="video-si-item video-comment">{{item.comments}}</span>
                    </div>
                  </dd>
                </dl>
              </li>
            </ul>
          </section>
        </div>

      </div>
  </div>
</template>

<script scoped>
  import video from './js/content.js';
  export default video;
</script>

<!-- Add "scoped" attribute to limit CSS to this component only @import './login.styl' -->
<style scoped lang="stylus">
    @import './stylus/video.styl'
</style>


